import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useUnifiedToast } from '../services/unifiedToastService';
import {
    Smartphone,
    ArrowLeft,
    Shield,
    Clock,
    Lock,
    KeyRound,
    ArrowRight,
    Fingerprint,
    Cpu,
    RefreshCw,
    Eye,
    EyeOff,
    CheckCircle
} from 'lucide-react';
import {
    UnifiedCard,
    SharedStyles,
    UnifiedBackground
} from './SharedTheme';

const ForgotPassword = () => {
    const navigate = useNavigate();
    const { showSuccess, showError } = useUnifiedToast();

    // Form states
    const [step, setStep] = useState('request');
    const [mobileNumber, setMobileNumber] = useState('');
    const [otp, setOtp] = useState(['', '', '', '', '', '']);
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [showNewPassword, setShowNewPassword] = useState(false);
    const [showConfirmPassword, setShowConfirmPassword] = useState(false);
    const [loading, setLoading] = useState(false);
    const [resendTimer, setResendTimer] = useState(0);
    const [verifiedMobile, setVerifiedMobile] = useState('');

    const otpRefs = useRef([]);

    useEffect(() => {
        if (resendTimer > 0) {
            const timer = setTimeout(() => setResendTimer(prev => prev - 1), 1000);
            return () => clearTimeout(timer);
        }
    }, [resendTimer]);

    useEffect(() => {
        if (step === 'verify' && otpRefs.current[0]) {
            otpRefs.current[0].focus();
        }
    }, [step]);

    const formatMobileNumber = (value) => value.replace(/[^\d]/g, '');

    const validateMobileNumber = (number) => {
        const cleaned = number.replace(/[^\d]/g, '');
        return cleaned.length >= 10 && cleaned.length <= 15;
    };

    const validatePassword = (password) => {
        const minLength = password.length >= 8;
        const hasUpper = /[A-Z]/.test(password);
        const hasLower = /[a-z]/.test(password);
        const hasNumber = /\d/.test(password);
        const hasSpecial = /[@$!%*?&]/.test(password);

        return {
            isValid: minLength && hasUpper && hasLower && hasNumber && hasSpecial,
            requirements: {
                minLength,
                hasUpper,
                hasLower,
                hasNumber,
                hasSpecial
            }
        };
    };

    const handleOTPChange = (index, value) => {
        if (value && !/^\d$/.test(value)) return;
        const newOtp = [...otp];
        newOtp[index] = value;
        setOtp(newOtp);

        if (value && index < 5 && otpRefs.current[index + 1]) {
            otpRefs.current[index + 1].focus();
        }
    };

    const handleOTPKeyDown = (index, e) => {
        if (e.key === 'Backspace' && !otp[index] && index > 0) {
            otpRefs.current[index - 1].focus();
        }
    };

    const getOTPValue = () => otp.join('');
    const getApiUrl = () => process.env.REACT_APP_API_URL || 'http://localhost:5001/api';

    const requestOTP = async () => {
        if (!validateMobileNumber(mobileNumber)) {
            showError('Invalid terminal ID');
            return;
        }

        setLoading(true);
        try {
            const formattedMobile = formatMobileNumber(mobileNumber);
            const response = await fetch(`${getApiUrl()}/password-reset/request-otp`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ mobileNumber: formattedMobile })
            });

            const data = await response.json();

            if (data.success && data.registered) {
                showSuccess(data.message);
                setStep('verify');
                setResendTimer(300);
            } else {
                showError(data.message || 'Terminal ID not found.');
            }
        } catch (error) {
            showError('Sync failure.');
        } finally {
            setLoading(false);
        }
    };

    const verifyOTPOnly = async () => {
        const otpValue = getOTPValue();
        if (otpValue.length !== 6) {
            showError('Complete authentication required');
            return;
        }

        setLoading(true);
        try {
            const formattedMobile = formatMobileNumber(mobileNumber);
            const response = await fetch(`${getApiUrl()}/password-reset/verify-otp-only`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ mobileNumber: formattedMobile, otp: otpValue })
            });

            const data = await response.json();

            if (data.success) {
                showSuccess('Hash verified.');
                setVerifiedMobile(formattedMobile);
                setStep('newPassword');
            } else {
                showError(data.message || 'Verification failed');
            }
        } catch (error) {
            showError('Sync failure.');
        } finally {
            setLoading(false);
        }
    };

    const resetPassword = async () => {
        const passwordValidation = validatePassword(newPassword);
        if (!passwordValidation.isValid) return;

        if (newPassword !== confirmPassword) {
            showError('Cipher mismatch');
            return;
        }

        setLoading(true);
        try {
            const response = await fetch(`${getApiUrl()}/password-reset/reset-password`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    mobileNumber: verifiedMobile,
                    newPassword: newPassword
                })
            });

            const data = await response.json();

            if (data.success) {
                showSuccess('Credentials updated.');
                setStep('success');
                setTimeout(() => navigate('/login'), 5000);
            } else {
                showError(data.message || 'Update failed');
            }
        } catch (error) {
            showError('Sync failure.');
        } finally {
            setLoading(false);
        }
    };

    const resendOTP = async () => {
        if (resendTimer > 0) return;
        setLoading(true);
        try {
            const response = await fetch(`${getApiUrl()}/password-reset/request-otp`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ mobileNumber: formatMobileNumber(mobileNumber) })
            });
            const data = await response.json();
            if (data.success) {
                showSuccess('New key sent');
                setResendTimer(300);
                setOtp(['', '', '', '', '', '']);
            }
        } catch (error) {
            showError('Sync failure.');
        } finally {
            setLoading(false);
        }
    };

    return (
        <UnifiedBackground minHeight="min-h-screen">
            <SharedStyles />
            <style>{`
                @keyframes gridFade {
                    0% { background-position: 0 0; }
                    100% { background-position: 0 50px; }
                }
                .bg-grid {
                    background-image: 
                        linear-gradient(rgba(249, 115, 22, 0.03) 1px, transparent 1px),
                        linear-gradient(90deg, rgba(249, 115, 22, 0.03) 1px, transparent 1px);
                    background-size: 50px 50px;
                    animation: gridFade 20s linear infinite;
                }
                .status-dot {
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: #f97316;
                    box-shadow: 0 0 10px #f97316;
                }
            `}</style>

            <div className="relative h-screen flex flex-col justify-center items-center px-6 pt-24 pb-12 bg-grid overflow-hidden">
                {/* Header Section (Compressed) */}
                <div className="flex flex-col items-center space-y-3 text-center mb-6">
                    <div className="relative w-12 h-12 bg-slate-900 border border-orange-500/30 rounded-xl flex items-center justify-center">
                        <Shield className="w-6 h-6 text-orange-500" />
                    </div>
                    <div className="space-y-0.5">
                        <h1 className="text-2xl font-black text-white tracking-widest uppercase">Secure Recovery</h1>
                        <p className="text-slate-500 text-[8px] font-black uppercase tracking-[0.3em]">Authorized Access Only</p>
                    </div>
                </div>

                <div className="w-full max-w-[400px]">
                    <UnifiedCard variant="glassDark" className="relative p-0.5 bg-slate-950/40 border-slate-800/50 rounded-[2rem] shadow-2xl overflow-hidden group">
                        <div className="m-6 space-y-6">
                            {step === 'request' && (
                                <div className="space-y-6 animate-in">
                                    <p className="text-center text-slate-400 text-[11px] font-bold leading-relaxed tracking-wider">
                                        Enter your registered phone number to authorize a secure password reset protocol.
                                    </p>

                                    <div className="space-y-5">
                                        <div className="relative space-y-1.5">
                                            <div className="flex justify-between px-1">
                                                <label className="text-[8px] font-black text-slate-500 uppercase tracking-widest">Registered phone number <span className="text-red-500">*</span></label>
                                                <div className="status-dot"></div>
                                            </div>
                                            <div className="relative">
                                                <div className="absolute left-3.5 top-1/2 -translate-y-1/2 text-orange-500/50">
                                                    <Smartphone className="w-3.5 h-3.5" />
                                                </div>
                                                <input
                                                    type="tel"
                                                    value={mobileNumber}
                                                    onChange={(e) => setMobileNumber(e.target.value)}
                                                    placeholder=" Phone Number"
                                                    className="w-full pl-10 pr-4 py-3.5 bg-slate-900 border border-slate-800 rounded-xl text-white placeholder-slate-700 focus:outline-none focus:ring-1 focus:ring-orange-500/50 transition-all font-mono tracking-[0.2em] text-xs"
                                                />
                                            </div>
                                        </div>

                                        <button
                                            onClick={requestOTP}
                                            disabled={loading || !validateMobileNumber(mobileNumber)}
                                            className="w-full py-3.5 bg-orange-500 hover:bg-orange-400 disabled:bg-slate-800 transition-all rounded-xl text-white font-black uppercase tracking-widest text-[11px] flex items-center justify-center space-x-2 group/btn shadow-[0_0_20px_rgba(249,115,22,0.1)]"
                                        >
                                            {loading ? <RefreshCw className="w-4 h-4 animate-spin" /> : (
                                                <>
                                                    <span>Request Access</span>
                                                    <ArrowRight className="w-3.5 h-3.5 group-hover/btn:translate-x-1 transition-transform" />
                                                </>
                                            )}
                                        </button>
                                    </div>
                                </div>
                            )}

                            {step === 'verify' && (
                                <div className="space-y-6 animate-in">
                                    <div className="text-center">
                                        <p className="text-slate-400 text-[10px] font-bold tracking-wider mb-2 uppercase">Identity Check Required:</p>
                                        <p className="text-orange-500 font-mono text-sm tracking-widest bg-orange-500/5 py-1.5 rounded-lg border border-orange-500/10 inline-block px-4">
                                            {mobileNumber.slice(-4).padStart(10, '*')}
                                        </p>
                                    </div>

                                    <div className="space-y-6">
                                        <div className="flex justify-between space-x-2">
                                            {otp.map((digit, index) => (
                                                <input
                                                    key={index}
                                                    ref={el => otpRefs.current[index] = el}
                                                    type="text"
                                                    value={digit}
                                                    onChange={(e) => handleOTPChange(index, e.target.value)}
                                                    onKeyDown={(e) => handleOTPKeyDown(index, e)}
                                                    className="w-full h-12 text-center text-lg font-black bg-slate-900 border border-slate-800 rounded-lg text-white focus:outline-none focus:ring-1 focus:ring-orange-500/50 transition-all font-mono"
                                                    maxLength={1}
                                                    inputMode="numeric"
                                                />
                                            ))}
                                        </div>

                                        <button
                                            onClick={verifyOTPOnly}
                                            disabled={loading || getOTPValue().length !== 6}
                                            className="w-full py-3.5 bg-orange-500 hover:bg-orange-400 disabled:bg-slate-800 transition-all rounded-xl text-white font-black uppercase tracking-widest text-[11px] flex items-center justify-center space-x-2 shadow-[0_0_20px_rgba(249,115,22,0.1)]"
                                        >
                                            {loading ? <RefreshCw className="w-4 h-4 animate-spin" /> : <span>Verify Key</span>}
                                        </button>

                                        <div className="flex justify-between items-center px-1">
                                            <div className="flex items-center space-x-1.5 text-slate-500">
                                                <Clock className="w-2.5 h-2.5" />
                                                <span className="text-[9px] font-black font-mono">
                                                    {Math.floor(resendTimer / 60)}:{(resendTimer % 60).toString().padStart(2, '0')}
                                                </span>
                                            </div>
                                            <button
                                                onClick={resendOTP}
                                                disabled={resendTimer > 0 || loading}
                                                className={`text-[8px] font-black uppercase tracking-widest transition-colors ${resendTimer > 0 ? 'text-slate-700' : 'text-orange-500 hover:text-orange-400'}`}
                                            >
                                                Resend Protocol
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            )}

                            {step === 'newPassword' && (
                                <div className="space-y-6 animate-in">
                                    <div className="space-y-4">
                                        <div className="relative space-y-1.5">
                                            <label className="text-[8px] font-black text-slate-500 uppercase tracking-widest ml-1">Establish Cipher</label>
                                            <div className="relative">
                                                <div className="absolute left-3.5 top-1/2 -translate-y-1/2 text-orange-500/50">
                                                    <Lock className="w-3.5 h-3.5" />
                                                </div>
                                                <input
                                                    type={showNewPassword ? "text" : "password"}
                                                    value={newPassword}
                                                    onChange={(e) => setNewPassword(e.target.value)}
                                                    placeholder="New Password"
                                                    className="w-full pl-10 pr-10 py-3 bg-slate-900 border border-slate-800 rounded-xl text-white placeholder-slate-700 focus:outline-none focus:ring-1 focus:ring-orange-500/50 transition-all font-mono text-xs"
                                                />
                                                <button onClick={() => setShowNewPassword(!showNewPassword)} className="absolute right-3.5 top-1/2 -translate-y-1/2 text-slate-600 hover:text-white">
                                                    {showNewPassword ? <EyeOff className="w-3.5 h-3.5" /> : <Eye className="w-3.5 h-3.5" />}
                                                </button>
                                            </div>
                                        </div>

                                        <div className="grid grid-cols-2 gap-1.5">
                                            {Object.entries(validatePassword(newPassword).requirements).map(([key, value]) => (
                                                <div key={key} className="flex items-center space-x-1.5 p-1.5 bg-white/5 rounded-lg border border-white/5">
                                                    <div className={`w-1 h-1 rounded-full ${value ? 'bg-orange-500 shadow-[0_0_5px_#f97316]' : 'bg-slate-700'}`}></div>
                                                    <span className={`text-[7px] font-black uppercase tracking-widest ${value ? 'text-orange-400' : 'text-slate-600'}`}>
                                                        {key === 'minLength' ? '8+ Len' : key === 'hasUpper' ? 'Upper' : key === 'hasLower' ? 'Lower' : key === 'hasNumber' ? 'Digit' : 'Symbol'}
                                                    </span>
                                                </div>
                                            ))}
                                        </div>

                                        <div className="relative">
                                            <div className="absolute left-3.5 top-1/2 -translate-y-1/2 text-orange-500/50">
                                                <KeyRound className="w-3.5 h-3.5" />
                                            </div>
                                            <input
                                                type={showConfirmPassword ? "text" : "password"}
                                                value={confirmPassword}
                                                onChange={(e) => setConfirmPassword(e.target.value)}
                                                placeholder="Confirm Cipher"
                                                className="w-full pl-10 pr-10 py-3 bg-slate-900 border border-slate-800 rounded-xl text-white placeholder-slate-700 focus:outline-none focus:ring-1 focus:ring-orange-500/50 transition-all font-mono text-xs"
                                            />
                                        </div>

                                        <button
                                            onClick={resetPassword}
                                            disabled={loading || !validatePassword(newPassword).isValid || newPassword !== confirmPassword}
                                            className="w-full py-3.5 bg-orange-500 hover:bg-orange-400 disabled:bg-slate-800 transition-all rounded-xl text-white font-black uppercase tracking-widest text-[11px] flex items-center justify-center space-x-2 shadow-[0_0_20px_rgba(249,115,22,0.1)]"
                                        >
                                            {loading ? <RefreshCw className="w-4 h-4 animate-spin" /> : <span>Realign credentials</span>}
                                        </button>
                                    </div>
                                </div>
                            )}

                            {step === 'success' && (
                                <div className="text-center space-y-6 animate-in py-4">
                                    <div className="relative inline-block">
                                        <div className="absolute inset-0 bg-orange-500/20 blur-2xl rounded-full"></div>
                                        <div className="relative w-16 h-16 bg-slate-900 border border-orange-500/30 rounded-full flex items-center justify-center mx-auto">
                                            <CheckCircle className="w-8 h-8 text-orange-500" />
                                        </div>
                                    </div>
                                    <div className="space-y-1">
                                        <h2 className="text-xl font-black text-white tracking-widest uppercase">Sync Complete</h2>
                                        <p className="text-slate-400 text-[8px] font-bold uppercase tracking-[0.2em]">Redirecting shortly</p>
                                    </div>
                                    <div className="flex justify-center space-x-1">
                                        {[1, 2, 3].map(i => <div key={i} className="w-1 h-1 bg-orange-500 rounded-full animate-bounce" style={{ animationDelay: `${i * 0.2}s` }}></div>)}
                                    </div>
                                </div>
                            )}

                            {step !== 'success' && (
                                <button
                                    onClick={() => step === 'request' ? navigate('/login') : setStep('request')}
                                    className="w-full py-1.5 flex items-center justify-center space-x-2 text-slate-600 hover:text-white transition-colors text-[8px] font-black uppercase tracking-widest border-t border-white/5 pt-4"
                                >
                                    <ArrowLeft className="w-2.5 h-2.5" />
                                    <span>{step === 'request' ? 'Return to Portal' : 'Reset Phase'}</span>
                                </button>
                            )}
                        </div>
                    </UnifiedCard>

                    {/* Trust Indicators (Compressed) */}
                    <div className="flex justify-between items-center px-4 pt-6 opacity-30">
                        <div className="flex flex-col">
                            <span className="text-[8px] font-black text-white uppercase tracking-widest">End-to-End</span>
                            <span className="text-[6px] font-bold text-slate-500 uppercase tracking-widest">Security Link</span>
                        </div>
                        <Cpu className="w-3.5 h-3.5 text-orange-500" />
                        <div className="flex flex-col text-right">
                            <span className="text-[8px] font-black text-white uppercase tracking-widest">256 AES-GCM</span>
                            <span className="text-[7px] font-bold text-slate-500 uppercase tracking-widest">Encryption Mode</span>
                        </div>
                    </div>
                </div>
            </div>
        </UnifiedBackground>
    );
};

export default ForgotPassword;
