/**
 * Authentication Service
 * Supports email/password login and Customer ID (Card No) login
 */

import { supabase } from './supabaseClient';

class AuthService {
  constructor() {
    this.currentUser = null;
    this.currentSession = null;

    // Restore state from localStorage on initialization
    // This ensures the singleton has the correct state even after page refresh/navigation
    this.restoreFromLocalStorage();
  }

  /**
   * Restore user and session from localStorage
   * Called on initialization to maintain state across navigation
   */
  restoreFromLocalStorage() {
    try {
      const rawLocalSession = localStorage.getItem('bp_local_session');
      const rawLocalUser = localStorage.getItem('bp_user');

      if (rawLocalSession && rawLocalUser) {
        try {
          const restoredSession = JSON.parse(rawLocalSession);
          const restoredUser = JSON.parse(rawLocalUser);

          // Validate session hasn't expired (24 hours)
          const expiresAt = restoredSession.expires_at;
          const now = Math.floor(Date.now() / 1000);

          if (expiresAt && expiresAt > now) {
            // Session is valid - restore it
            this.currentUser = restoredUser;
            this.currentSession = restoredSession;
          } else {
            // Session expired - clear it
            localStorage.removeItem('bp_local_session');
            localStorage.removeItem('bp_user');
          }
        } catch (parseError) {
          // Invalid JSON - clear it
          localStorage.removeItem('bp_local_session');
          localStorage.removeItem('bp_user');
        }
      }
    } catch (error) {
      // Ignore localStorage errors (e.g., in SSR or private browsing)
    }
  }

  /**
   * Simple email/password login
   */
  async login(email, password) {
    try {
      console.log(' [AuthService] login() called', { email, passwordLength: password?.length });

      // Input validation
      if (!email || !password) {
        console.error(' [AuthService] Missing email or password');
        throw new Error('Please provide both email and password');
      }

      // Validate email format
      if (!/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/.test(email)) {
        console.error(' [AuthService] Invalid email format:', email);
        throw new Error('Please enter a valid email address');
      }

      console.log(' [AuthService] Calling supabase.auth.signInWithPassword...');
      // Attempt authentication with Supabase
      const { data, error } = await supabase.auth.signInWithPassword({
        email: email,
        password: password
      });

      if (error) {
        console.error(' [AuthService] Supabase auth error:', error.message);
        // Provide user-friendly error messages
        if (error.message.includes('Invalid login credentials')) {
          throw new Error('Invalid email or password');
        } else if (error.message.includes('Email not confirmed')) {
          throw new Error('Please check your email and click the confirmation link before signing in.');
        } else if (error.message.includes('Too many requests')) {
          throw new Error('Too many login attempts. Please wait a few minutes before trying again.');
        } else {
          throw new Error(`Login failed: ${error.message}`);
        }
      }

      if (!data.user) {
        console.error(' [AuthService] No user data in response');
        throw new Error('Login failed: No user data received');
      }

      console.log(' [AuthService] Supabase auth successful, user ID:', data.user.id);
      console.log(' [AuthService] Fetching user profile from database...');

      // Get user profile from database
      const { data: profile, error: profileError } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', data.user.id)
        .single();

      if (profileError) {
        console.error(' [AuthService] Profile fetch error:', profileError);
        throw new Error('Could not load user profile');
      }

      if (!profile) {
        console.error(' [AuthService] No profile found for user:', data.user.id);
        throw new Error('Could not load user profile');
      }

      console.log(' [AuthService] Profile loaded:', {
        id: profile.id,
        name: profile.name,
        role: profile.role,
        email: profile.email
      });

      // Note: is_active field removed from schema - all users are considered active

      // Create user profile object
      const userProfile = {
        ...profile,
        id: data.user.id,
        // Ensure standard fields are mapped correctly if names differ
        email: profile.email,
        name: profile.name,
        role: profile.role,
        // Map backend underscore names to frontend camelCase if necessary
        aadhaarNumber: profile.aadhaarNumber || profile.aadhaar_number,
        panNumber: profile.panNumber || profile.pan_number,
        phone: profile.phone,
        address: profile.address
      };

      console.log(' [AuthService] Storing user and session in authService');
      // Store current user and session
      this.currentUser = userProfile;
      this.currentSession = data.session;

      console.log(' [AuthService] Login complete, returning userProfile:', {
        id: userProfile.id,
        role: userProfile.role,
        name: userProfile.name
      });

      return userProfile;

    } catch (error) {
      console.error(' [AuthService] Login exception:', error.message);
      console.error(' [AuthService] Error stack:', error.stack);
      throw error;
    }
  }

  /**
   * Customer login with Card No (Customer ID) and password
   * Uses database function for authentication (password verified in database)
   */
  async loginWithCardNo(cardNo, password) {
    try {

      // Input validation
      if (!cardNo || !password) {
        throw new Error('Please provide both Customer ID and password');
      }

      // Call the secure backend API instead of direct Supabase RPC
      const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';

      const response = await fetch(`${API_URL}/auth/customer`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          customerId: cardNo.trim().toUpperCase(),
          password: password
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data?.message || data?.error || 'Invalid Customer ID or password');
      }

      const userProfile = data.user;

      if (!userProfile || !userProfile.id) {
        throw new Error('Authentication failed: Invalid user data received');
      }

      // Create a local session object for authentication state
      // Note: We use local sessions because customer passwords use pgcrypto hashing
      // which Supabase Auth doesn't support (it uses pbkdf2)
      const localSession = {
        access_token: `local_session_${userProfile.id}`,
        token_type: 'bearer',
        user: {
          id: userProfile.id,
          email: data.auth_email || userProfile.email
        },
        expires_at: Math.floor(Date.now() / 1000) + (3600 * 24) // 24 hours
      };

      // Store current user and session
      this.currentUser = {
        ...userProfile,
        role: userProfile.role || 'customer'
      };
      this.currentSession = localSession;

      // Persist local session to survive refresh
      try {
        localStorage.setItem('bp_local_session', JSON.stringify(localSession));
        localStorage.setItem('bp_user', JSON.stringify(this.currentUser));
      } catch (_) { }

      return this.currentUser;

    } catch (error) {
      throw error;
    }
  }

  /**
   * Promoter login with Promoter ID and password (ONLY METHOD)
   * Uses database function for direct authentication
   */
  async loginWithPromoterID(promoterID, password) {
    try {

      // Input validation
      if (!promoterID || !password) {
        throw new Error('Please provide both Promoter ID and password');
      }

      // Validate Promoter ID format (BPVP followed by digits)
      if (!/^BPVP\d+$/i.test(promoterID)) {
        throw new Error('Invalid Promoter ID format. Expected format: BPVP01, BPVP02, etc.');
      }

      // Call the secure backend API instead of direct Supabase RPC
      const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';

      const response = await fetch(`${API_URL}/auth/promoter`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          promoterId: promoterID.toUpperCase(),
          password: password
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data?.message || data?.error || 'Invalid Promoter ID or password');
      }

      const userProfile = data.user;
      console.log('[AuthService] Promoter login successful. User profile from DB:', userProfile);

      // Create a local session object for authentication state
      // Note: We skip Supabase Auth session since promoter passwords use pgcrypto hashing
      // which Supabase Auth doesn't support (it uses pbkdf2)
      const localSession = {
        access_token: `local_session_${userProfile.id}`,
        token_type: 'bearer',
        user: {
          id: userProfile.id,
          email: data.auth_email
        },
        expires_at: Math.floor(Date.now() / 1000) + (3600 * 24) // 24 hours
      };

      // Store current user and session
      this.currentUser = userProfile;
      this.currentSession = localSession;

      // Persist local session to survive refresh
      try {
        localStorage.setItem('bp_local_session', JSON.stringify(localSession));
        localStorage.setItem('bp_user', JSON.stringify(userProfile));
      } catch (_) { }

      return userProfile;

    } catch (error) {
      throw error;
    }
  }

  /**
   * Logout user
   */
  async logout() {
    try {
      // Sign out from Supabase
      const { error } = await supabase.auth.signOut();
      if (error) {
      }

      // Clear local state
      this.currentUser = null;
      this.currentSession = null;

      // Clear any persisted local session
      try {
        localStorage.removeItem('bp_local_session');
        localStorage.removeItem('bp_user');
      } catch (_) { }

    } catch (error) {
      // Clear local state even if server logout fails
      this.currentUser = null;
      this.currentSession = null;

      try {
        localStorage.removeItem('bp_local_session');
        localStorage.removeItem('bp_user');
      } catch (_) { }
    }
  }

  /**
   * Get current user
   */
  getCurrentUser() {
    return this.currentUser;
  }

  /**
   * Get current session
   */
  getCurrentSession() {
    return this.currentSession;
  }

  /**
   * Check if user is authenticated
   */
  isAuthenticated() {
    return !!this.currentUser && !!this.currentSession;
  }

  /**
   * Validate current session
   */
  async validateSession() {
    try {
      const { data: { session }, error } = await supabase.auth.getSession();

      if (error) {
        // Handle refresh token errors gracefully
        if (error.message?.includes('Refresh Token Not Found') ||
          error.message?.includes('Invalid Refresh Token')) {
          console.warn('Invalid or expired refresh token, clearing session');
          await this.logout();
          return false;
        }

        this.currentUser = null;
        this.currentSession = null;
        return false;
      }

      if (!session) {
        this.currentUser = null;
        this.currentSession = null;
        return false;
      }

      this.currentSession = session;
      return true;
    } catch (error) {
      console.error('Session validation error:', error);
      // Clear session on any error
      await this.logout();
      return false;
    }
  }
}

// Create singleton instance
const authService = new AuthService();

// Export the service
export default authService;
