import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const NotFoundRedirect = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { user, loading } = useAuth();

  useEffect(() => {
    if (loading) return;
    if (!user) {
      navigate('/login', { replace: true, state: { from: location.pathname } });
      return;
    }
    switch (user.role) {
      case 'admin':
        navigate('/admin/dashboard', { replace: true });
        break;
      case 'promoter':
        navigate('/promoter/home', { replace: true });
        break;
      case 'customer':
        navigate('/customer/coupons', { replace: true });
        break;
      default:
        navigate('/', { replace: true });
        break;
    }
  }, [user, loading, navigate, location.pathname]);

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-900">
      <div className="text-center">
        <div className="animate-spin rounded-full h-14 w-14 border-4 border-blue-400/30 border-t-blue-400 mx-auto mb-4"></div>
        <p className="text-gray-300">Redirecting to your dashboard</p>
      </div>
    </div>
  );
};

export default NotFoundRedirect;