/**
 * Mobile Number Validation Service
 * Provides validation for mobile number uniqueness and formatting
 */

import { supabase } from './supabaseClient.js';

class MobileValidationService {
  /**
   * Validate mobile number uniqueness across all users
   * @param {string} mobileNumber - Mobile number to validate
   * @param {string} userId - Current user ID (for updates)
   * @param {string} excludeUserId - User ID to exclude from check
   * @returns {Promise<Object>} Validation result
   */
  /**
   * Validate mobile number uniqueness across all users
   * @param {string} mobileNumber - Mobile number to validate
   * @param {string} userId - Current user ID (for updates)
   * @param {string} excludeUserId - User ID to exclude from check
   * @param {string} role - Role of the user ('customer' or 'promoter')
   * @returns {Promise<Object>} Validation result
   */
  static async validateMobileNumber(mobileNumber, userId = null, excludeUserId = null, role = null) {
    try {
      // BUSINESS RULE: Customer mobile numbers do NOT need to be unique.
      // If role is 'customer', we bypass the server check entirely.
      // This assumes the backend database constraints have been relaxed.
      if (role === 'customer') {
        return {
          success: true,
          message: 'Mobile number is acceptable',
          is_valid: true,
          is_customer_bypass: true
        };
      }

      // For promoters (or unspecified role), we check uniqueness.
      // Note: We use the 3-argument RPC call for compatibility with both old and new DB functions.
      // The new DB function uses a default value for the 4th argument (role).
      const { data, error } = await supabase.rpc('validate_mobile_number', {
        p_mobile: mobileNumber,
        p_user_id: userId,
        p_exclude_user_id: excludeUserId
      });

      if (error) {
        console.error('Mobile validation error:', error);
        return {
          success: false,
          error: error.message,
          is_valid: false
        };
      }

      // Enhanced error messages for better UX
      if (!data.success && data.error) {
        let enhancedError = data.error;

        if (data.error.includes('already registered')) {
          enhancedError = 'This mobile number is already registered in the system. Please use a different number.';

          // Add details about existing users if available
          if (data.existing_users && data.existing_users.length > 0) {
            const existingUserTypes = data.existing_users.map(u => u.role).filter(Boolean);
            if (existingUserTypes.length > 0) {
              enhancedError += ` Already registered as: ${existingUserTypes.join(', ')}.`;
            }
          }
        }

        return {
          ...data,
          error: enhancedError
        };
      }

      return data;
    } catch (error) {
      console.error('Mobile validation service error:', error);
      return {
        success: false,
        error: 'Mobile validation service unavailable',
        is_valid: false
      };
    }
  }

  /**
   * Format mobile number to Indian standard
   * @param {string} mobileNumber - Mobile number to format
   * @returns {Promise<Object>} Formatted result
   */
  static async formatMobileNumber(mobileNumber) {
    try {
      const { data, error } = await supabase.rpc('format_mobile_number', {
        p_mobile: mobileNumber
      });

      if (error) {
        console.error('Mobile formatting error:', error);
        return {
          success: false,
          error: error.message,
          formatted_mobile: null
        };
      }

      return data;
    } catch (error) {
      console.error('Mobile formatting service error:', error);
      return {
        success: false,
        error: 'Mobile formatting service unavailable',
        formatted_mobile: null
      };
    }
  }

  /**
   * Check system-wide mobile number uniqueness
   * @returns {Promise<Object>} Uniqueness report
   */
  static async checkMobileUniqueness() {
    try {
      const { data, error } = await supabase.rpc('check_mobile_uniqueness');

      if (error) {
        console.error('Mobile uniqueness check error:', error);
        return {
          success: false,
          error: error.message
        };
      }

      return data;
    } catch (error) {
      console.error('Mobile uniqueness check error:', error);
      return {
        success: false,
        error: 'Mobile uniqueness check unavailable'
      };
    }
  }

  /**
   * Get mobile number statistics
   * @returns {Promise<Object>} Statistics
   */
  static async getMobileStats() {
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/mobile-validation/stats`);
      const data = await response.json();

      return data;
    } catch (error) {
      console.error('Mobile stats error:', error);
      return {
        success: false,
        error: 'Mobile statistics unavailable'
      };
    }
  }

  /**
   * Validate mobile number format (client-side)
   * @param {string} mobileNumber - Mobile number to validate
   * @returns {Object} Validation result
   */
  static validateMobileFormat(mobileNumber) {
    const cleanMobile = mobileNumber.replace(/[^\d]/g, '');

    // Basic validation
    if (!mobileNumber || mobileNumber.trim() === '') {
      return {
        is_valid: false,
        error: 'Mobile number is required',
        clean_mobile: null
      };
    }

    // Length check
    if (cleanMobile.length !== 10) {
      return {
        is_valid: false,
        error: 'Mobile number must be exactly 10 digits',
        clean_mobile: cleanMobile
      };
    }

    // Indian mobile number check (starts with 6-9)
    if (!/^[6-9]/.test(cleanMobile)) {
      return {
        is_valid: false,
        error: 'Mobile number must start with 6, 7, 8, or 9',
        clean_mobile: cleanMobile
      };
    }

    return {
      is_valid: true,
      error: null,
      clean_mobile: cleanMobile,
      formatted_mobile: '+91-' + cleanMobile
    };
  }

  /**
   * Real-time mobile number validation with debouncing
   * @param {string} mobileNumber - Mobile number to validate
   * @param {string} userId - Current user ID
   * @param {number} debounceMs - Debounce delay in milliseconds
   * @returns {Promise<Object>} Validation result
   */
  static validateMobileDebounced = (() => {
    let timeout;
    return async (mobileNumber, userId = null, excludeUserId = null, debounceMs = 500) => {
      clearTimeout(timeout);
      timeout = setTimeout(() => {
        return this.validateMobileNumber(mobileNumber, userId, excludeUserId);
      }, debounceMs);
    };
  })();

  /**
   * Check if mobile number is available (quick check)
   * @param {string} mobileNumber - Mobile number to check
   * @returns {Promise<boolean>} True if available
   */
  static async isMobileAvailable(mobileNumber) {
    try {
      const result = await this.validateMobileNumber(mobileNumber);
      return result.success && result.is_valid;
    } catch (error) {
      console.error('Mobile availability check error:', error);
      return false;
    }
  }

  /**
   * Get formatted mobile number display
   * @param {string} mobileNumber - Mobile number to format
   * @returns {string} Formatted mobile number
   */
  static getFormattedMobile(mobileNumber) {
    const cleanMobile = mobileNumber.replace(/[^\d]/g, '');
    if (cleanMobile.length === 10 && /^[6-9]/.test(cleanMobile)) {
      return '+91-' + cleanMobile;
    }
    return mobileNumber;
  }

  /**
   * Extract mobile number from various formats
   * @param {string} input - Input string
   * @returns {string} Clean mobile number
   */
  static extractMobileNumber(input) {
    if (!input) return '';

    // Remove all non-digit characters
    return input.replace(/[^\d]/g, '');
  }

  /**
   * Generate suggestions for invalid mobile numbers
   * @param {string} mobileNumber - Invalid mobile number
   * @returns {Array} Array of suggestions
   */
  static getMobileSuggestions(mobileNumber) {
    const suggestions = [];
    const cleanMobile = this.extractMobileNumber(mobileNumber);

    if (cleanMobile.length < 10) {
      suggestions.push('Mobile number must be exactly 10 digits');
      suggestions.push('Example: 9008955245');
    } else if (cleanMobile.length > 10) {
      suggestions.push('Mobile number must be exactly 10 digits');
      suggestions.push('Remove extra digits: ' + cleanMobile.substring(0, 10));
    } else if (!/^[6-9]/.test(cleanMobile)) {
      suggestions.push('Indian mobile numbers start with 6, 7, 8, or 9');
      suggestions.push('Example: 6XXXXXXXXXX, 7XXXXXXXXXX, 8XXXXXXXXXX, 9XXXXXXXXXX');
    }

    return suggestions;
  }
}

export default MobileValidationService;
