/**
 * Global Error Handler for Production
 * Handles common production errors gracefully
 */

// Suppress specific console errors in production
if (process.env.NODE_ENV === 'production') {
  const originalError = console.error;
  console.error = (...args) => {
    // Suppress known non-critical errors
    const errorString = args.join(' ');
    
    // Suppress refresh token errors (handled by auth service)
    if (errorString.includes('Refresh Token Not Found') ||
        errorString.includes('Invalid Refresh Token')) {
      return;
    }
    
    // Suppress manifest.json 403 errors (not critical for app functionality)
    if (errorString.includes('manifest.json') && errorString.includes('403')) {
      return;
    }
    
    // Suppress CSP violations for Google Fonts (now fixed in .htaccess)
    if (errorString.includes('Content Security Policy') && 
        errorString.includes('fonts.googleapis.com')) {
      return;
    }
    
    // Log all other errors normally
    originalError.apply(console, args);
  };
}

// Handle unhandled promise rejections
window.addEventListener('unhandledrejection', (event) => {
  const error = event.reason;
  
  // Handle Supabase auth errors gracefully
  if (error?.message?.includes('Refresh Token Not Found') ||
      error?.message?.includes('Invalid Refresh Token')) {
    console.warn('Session expired, clearing stale tokens');
    event.preventDefault();
    
    // Clear stale session data silently
    if (typeof window !== 'undefined') {
      window.localStorage.removeItem('brightplanet-auth');
      window.localStorage.removeItem('supabase.auth.token');
    }
    
    // DO NOT redirect - let the app handle authentication naturally
    // The AuthContext will detect no session and show login pages appropriately
    return;
  }
  
  // Log other unhandled rejections
  console.error('Unhandled promise rejection:', error);
});

// Handle global errors
window.addEventListener('error', (event) => {
  const error = event.error;
  
  // Suppress manifest.json errors
  if (event.message?.includes('manifest.json')) {
    event.preventDefault();
    return;
  }
  
  // Suppress CSP violations (now fixed)
  if (event.message?.includes('Content Security Policy')) {
    event.preventDefault();
    return;
  }
  
  console.error('Global error:', error);
});

export default {
  init: () => {
    // Error handler initialized
  }
};
