import React, { createContext, useState, useContext, useEffect } from 'react';
import { supabase } from "../services/supabaseClient"
import authService from '../services/authService';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [session, setSession] = useState(null);

  // Optimized function to load user profile with caching
  const loadUserProfile = async (authUser) => {
    try {
      // Check cache first
      const cachedProfile = sessionStorage.getItem(`profile_${authUser.id}`);
      if (cachedProfile) {
        return JSON.parse(cachedProfile);
      }

      // Reduced timeout for faster fallback
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('Profile loading timeout')), 5000);
      });

      const profilePromise = supabase
        .from('profiles')
        .select('*')
        .eq('id', authUser.id)
        .single();

      const { data: profile, error } = await Promise.race([profilePromise, timeoutPromise]);

      if (error || !profile) {

        // Fallback: create basic profile from auth user
        const fallbackProfile = {
          id: authUser.id,
          email: authUser.email,
          name: authUser.email.split('@')[0],
          role: authUser.email.includes('admin') ? 'admin' :
            authUser.email.includes('promoter') ? 'promoter' : 'customer'
        };

        return fallbackProfile;
      }

      const userProfile = {
        ...profile,
        id: authUser.id,
        // Ensure standard fields are mapped correctly if names differ
        email: profile.email,
        name: profile.name,
        role: profile.role,
        // Map backend underscore names to frontend camelCase if necessary
        // (Though some RPCs might already do this, it's safer to ensure they exist)
        aadhaarNumber: profile.aadhaarNumber || profile.aadhaar_number,
        panNumber: profile.panNumber || profile.pan_number,
        phone: profile.phone,
        address: profile.address
      };

      // Cache the profile for faster subsequent loads
      sessionStorage.setItem(`profile_${authUser.id}`, JSON.stringify(userProfile));

      return userProfile;


    } catch (error) {

      // Fallback profile
      const fallbackProfile = {
        id: authUser.id,
        email: authUser.email,
        name: authUser.email.split('@')[0],
        role: authUser.email.includes('admin') ? 'admin' :
          authUser.email.includes('promoter') ? 'promoter' : 'customer'
      };

      return fallbackProfile;
    }
  };

  // Helper function to restore session from localStorage
  const restoreLocalSession = () => {
    try {
      const rawLocalSession = localStorage.getItem('bp_local_session');
      const rawLocalUser = localStorage.getItem('bp_user');

      if (rawLocalSession && rawLocalUser) {
        try {
          const restoredSession = JSON.parse(rawLocalSession);
          const restoredUser = JSON.parse(rawLocalUser);

          // Validate session hasn't expired (24 hours)
          const expiresAt = restoredSession.expires_at;
          const now = Math.floor(Date.now() / 1000);

          if (expiresAt && expiresAt > now) {
            // Session is valid - restore immediately
            setSession(restoredSession);
            setUser(restoredUser);

            // Also restore in authService singleton
            if (authService) {
              authService.currentUser = restoredUser;
              authService.currentSession = restoredSession;
            }

            return true; // Session restored
          } else {
            // Session expired - clear it
            localStorage.removeItem('bp_local_session');
            localStorage.removeItem('bp_user');
            return false;
          }
        } catch (parseError) {
          // Invalid JSON - clear it
          localStorage.removeItem('bp_local_session');
          localStorage.removeItem('bp_user');
          return false;
        }
      }
      return false;
    } catch (localError) {
      // Ignore localStorage errors
      return false;
    }
  };

  // Optimized initial session loading - check localStorage FIRST for customer/promoter sessions
  useEffect(() => {
    console.log(' [AuthContext] Initial session loading started');
    const getInitialSession = async () => {
      try {
        const forcedLogout = (() => {
          try {
            return localStorage.getItem('bp_forced_logout');
          } catch (_) {
            return null;
          }
        })();

        if (forcedLogout === 'true') {
          console.log(' [AuthContext] Forced logout detected in initial load');
          setUser(null);
          setSession(null);
          setLoading(false);
          return;
        }

        // FIRST: Check for local session (customer/promoter) - this is synchronous and fast
        // This prevents logout on back navigation for customer/promoter users
        console.log(' [AuthContext] Checking for local session...');
        const localSessionRestored = restoreLocalSession();

        if (localSessionRestored) {
          console.log(' [AuthContext] Local session restored');
          setLoading(false);
          return; // Exit early - don't check Supabase session
        }

        console.log(' [AuthContext] No local session, checking Supabase session...');
        // SECOND: Check for Supabase session (admin users)
        const { data: { session }, error } = await supabase.auth.getSession();

        // Handle refresh token errors gracefully
        if (error) {
          console.error(' [AuthContext] Error getting Supabase session:', error.message);
          if (error.message?.includes('Refresh Token Not Found') ||
            error.message?.includes('Invalid Refresh Token')) {
            console.warn(' [AuthContext] Stale session detected, clearing...');
            sessionStorage.removeItem('supabase_session');
            setLoading(false);
            return;
          }
        }

        if (session?.user) {
          console.log(' [AuthContext] Supabase session found, user ID:', session.user.id);
          setSession(session);
          sessionStorage.setItem('supabase_session', 'true');

          // Load profile asynchronously to not block UI
          console.log(' [AuthContext] Loading user profile...');
          loadUserProfile(session.user)
            .then(profile => {
              console.log(' [AuthContext] Profile loaded in initial session:', {
                id: profile.id,
                role: profile.role,
                name: profile.name
              });
              setUser(profile);
              setLoading(false);
            })
            .catch(error => {
              console.error(' [AuthContext] Error loading profile in initial session:', error);
              setLoading(false);
            });
        } else {
          console.log(' [AuthContext] No Supabase session found');
          // No session found - clear cache and set loading to false
          sessionStorage.removeItem('supabase_session');
          setLoading(false);
        }
      } catch (error) {
        console.error('Session initialization error:', error);
        sessionStorage.removeItem('supabase_session');
        setLoading(false);
      }
    };
    getInitialSession();
  }, []);

  // Restore session when window regains focus (handles browser back button)
  useEffect(() => {
    const handleFocus = () => {
      // Only restore if we don't have a user but have a local session
      if (!user) {
        restoreLocalSession();
      }
    };

    const handleVisibilityChange = () => {
      // When page becomes visible again, check for session
      if (document.visibilityState === 'visible' && !user) {
        restoreLocalSession();
      }
    };

    window.addEventListener('focus', handleFocus);
    document.addEventListener('visibilitychange', handleVisibilityChange);

    return () => {
      window.removeEventListener('focus', handleFocus);
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [user]);

  // Optimized auth state listener - only for Supabase Auth (admin users)
  // Customer/promoter sessions are managed via localStorage and don't trigger this
  useEffect(() => {
    console.log(' [AuthContext] Setting up auth state listener');
    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (event, session) => {
      console.log(' [AuthContext] Auth state changed:', { event, hasSession: !!session, userId: session?.user?.id });

      let forcedLogout = null;
      try {
        forcedLogout = localStorage.getItem('bp_forced_logout');
      } catch (_) { }

      if (forcedLogout === 'true') {
        console.log(' [AuthContext] Forced logout detected, signing out');
        if (event === 'SIGNED_IN') {
          try {
            await supabase.auth.signOut();
          } catch (_) { }
        }
        setSession(null);
        setUser(null);
        sessionStorage.removeItem('supabase_session');
        setLoading(false);
        return;
      }

      // Only handle Supabase Auth events (admin users)
      // Customer/promoter logins use localStorage and won't trigger these events
      if (event === 'SIGNED_IN' && session?.user) {
        console.log(' [AuthContext] SIGNED_IN event detected, loading user profile');
        setSession(session);
        sessionStorage.setItem('supabase_session', 'true');

        // Load profile asynchronously
        loadUserProfile(session.user)
          .then(profile => {
            console.log(' [AuthContext] Profile loaded from auth state listener:', {
              id: profile.id,
              role: profile.role,
              name: profile.name
            });
            setUser(profile);
          })
          .catch(error => {
            console.error(' [AuthContext] Error loading profile in auth state listener:', error);
          });
      } else if (event === 'SIGNED_OUT') {
        console.log(' [AuthContext] SIGNED_OUT event detected');
        // Only clear Supabase session - don't clear localStorage sessions
        // (customer/promoter sessions should persist)
        setSession(null);
        // Only clear user if it was a Supabase session
        const currentSession = session || await supabase.auth.getSession().then(r => r.data.session);
        if (!currentSession) {
          // Check if we have a local session - if yes, keep user
          const hasLocalSession = localStorage.getItem('bp_local_session');
          if (!hasLocalSession) {
            setUser(null);
          }
        }
        sessionStorage.removeItem('supabase_session');
        // Clear profile cache only if no local session exists
        const hasLocalSession = localStorage.getItem('bp_local_session');
        if (!hasLocalSession) {
          Object.keys(sessionStorage).forEach(key => {
            if (key.startsWith('profile_')) {
              sessionStorage.removeItem(key);
            }
          });
        }
      }

      setLoading(false);
    });
    return () => {
      subscription.unsubscribe();
    };
  }, []);

  const login = async (identifier, password, role = null, method = null) => {
    console.log(' [AuthContext] login() called', {
      identifier,
      role,
      passwordLength: password?.length,
      currentUser: user?.id,
      currentSession: !!session
    });

    try {
      let userProfile;

      if (role === 'customer') {
        console.log(' [AuthContext] Customer login path');
        // Customer login with Card No
        userProfile = await authService.loginWithCardNo(identifier, password);
      } else if (role === 'promoter') {
        console.log(' [AuthContext] Promoter login path');
        // Promoter login - ONLY Promoter ID method
        userProfile = await authService.loginWithPromoterID(identifier, password);
      } else {
        console.log(' [AuthContext] Admin login path');
        // Admin login with email
        userProfile = await authService.login(identifier, password);
      }

      console.log(' [AuthContext] Login service returned userProfile:', {
        id: userProfile?.id,
        role: userProfile?.role,
        name: userProfile?.name
      });

      if (!userProfile) {
        console.error(' [AuthContext] userProfile is null/undefined!');
        throw new Error('Login failed: No user profile returned');
      }

      // IMPORTANT: Set user state immediately for ALL roles including admin
      // The auth state listener will also fire, but we need immediate state update for navigation
      console.log(' [AuthContext] Setting user state immediately for all roles');
      console.log(' [AuthContext] Current state before update:', {
        user: user?.id,
        session: !!session,
        loading
      });

      setUser(userProfile);
      const currentSession = authService.getCurrentSession();
      if (currentSession) {
        setSession(currentSession);
        console.log(' [AuthContext] Session also set');
      }

      console.log(' [AuthContext] User state set immediately');
      console.log(' [AuthContext] State after update should have user:', userProfile.id);

      // User state will be updated by the auth state change listener
      try {
        localStorage.removeItem('bp_forced_logout');
      } catch (_) { }

      console.log(' [AuthContext] Login complete, returning userProfile');
      return userProfile;

    } catch (error) {
      console.error(' [AuthContext] Login error:', error.message);
      console.error(' [AuthContext] Error stack:', error.stack);
      throw error;
    }
  };

  const logout = async () => {
    console.log('[AuthContext] Starting immediate logout...');
    console.trace('Logout called from:');

    // 1. Immediately clear all local state
    setUser(null);
    setSession(null);

    // 2. Clear all storage synchronously
    try {
      // Clear session storage
      sessionStorage.removeItem('supabase_session');

      // Clear local storage
      localStorage.removeItem('bp_local_session');
      localStorage.removeItem('bp_user');
      localStorage.setItem('bp_forced_logout', 'true');

      // Clear profile cache
      Object.keys(sessionStorage).forEach(key => {
        if (key.startsWith('profile_')) {
          sessionStorage.removeItem(key);
        }
      });

      // Clear auth service state
      if (authService) {
        authService.currentUser = null;
        authService.currentSession = null;
      }

      console.log('[AuthContext] Local session cleared');
    } catch (error) {
      console.error('[AuthContext] Error during cleanup:', error);
    }

    // 3. Fire and forget Supabase sign out (don't wait for it)
    supabase.auth.signOut()
      .then(() => console.log('[AuthContext] Supabase sign out completed in background'))
      .catch(e => console.warn('[AuthContext] Supabase sign out error (non-critical):', e.message));
  };

  const value = {
    user,
    loading,
    session,
    login,
    logout
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export default AuthContext;
